nav {
  position: fixed;
  width: 100%;
  transition: all .4s ease;
  z-index: 1; }
  nav h1 {
    border: 1px solid black;
    color: black;
    padding: 1rem 1rem;
    border-radius: 15px;
    text-align: center; }
    nav h1 span {
      color: white; }
  nav .barra {
    cursor: pointer; }
  nav .links {
    list-style: none; }
  @media (min-width: 320px) {
    nav h1 {
      margin: 2rem 1rem; }
    nav .opciones-menu {
      display: flex;
      justify-content: space-between;
      align-items: center; }
    nav .barra {
      font-size: 3rem;
      color: black;
      margin-right: 3rem; }
    nav .links {
      background-color: black;
      color: white;
      height: 100vh;
      width: 0vw;
      top: 0;
      position: absolute;
      overflow: hidden;
      transition: all .4s ease;
      z-index: 1; }
      nav .links .close {
        position: absolute;
        right: 0;
        margin: 1rem 1rem;
        font-size: 2rem; }
      nav .links li, nav .links a {
        width: 70%;
        border: 1px solid white;
        border-radius: 20px;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 1rem;
        padding: 1rem 2rem;
        transition: all .3s ease;
        text-decoration: none; }
        nav .links li:hover, nav .links a:hover {
          background-color: white;
          color: black;
          cursor: pointer; }
      nav .links .first {
        margin-top: 8rem !important; } }
  @media (min-width: 750px) {
    nav .barra {
      color: white; } }
  @media (min-width: 1200px) {
    nav .contenido {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1rem; }
    nav h1 {
      padding: .5rem 1rem;
      margin: 0; }
    nav .barra {
      display: none; }
    nav .links {
      width: 60%;
      background-color: transparent;
      display: flex;
      justify-content: space-between;
      position: relative;
      height: 5%; }
      nav .links .close {
        display: none; }
      nav .links li, nav .links a {
        background-color: black;
        color: white;
        padding: 0;
        height: 10%;
        width: 10%;
        padding: .8rem;
        text-decoration: none; }
      nav .links .first {
        margin-top: 0 !important; } }

